import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)

export default new Vuex.Store({
  state: { //data  
    dataCart:[],//
    buyData:[],//
    xuseraddress:[],//
    orderList:[] //
  },
  getters:{ //computed,state
    totalInfo(state){
      let total=0; //
      let totalPrice=0; //
      let buyPrice = 0; //
  
      let cstate = state.dataCart;
      let bcstate = state.buyData;
  
      cstate.forEach((val)=>{
        total+=val.count;
        totalPrice+=val.count*val.spec[0].ncPrice;
      });
  
      bcstate.forEach((val)=>{
        buyPrice=val.count*val.spec[0].ncPrice;
      });
  
      return {
        total,
        totalPrice,
        buyPrice
      }
    }
  },
  mutations: { //methodsstate
      cartAddMut(state,obj){  //
        
        let cstate = state.dataCart;
        let bool = true;
        cstate.forEach((val)=>{
            if(val.id===obj.id){
                val.count++;
                bool=false;
                return false;
            }
        })
        if(bool){
          Vue.set(obj,'count',1); //
          cstate.push(obj);
        }
        // console.log(obj);
      },
      countAdd(state,idx){  //
        let cstate = state.dataCart;
        cstate[idx].count++;
        if(cstate[idx].count>cstate[idx].limit_num){
           cstate[idx].count=cstate[idx].limit_num
        }
          
      },
      countSub(state,idx){  //
        let cstate = state.dataCart;
        cstate[idx].count--;
        if(cstate[idx].count<1){
           cstate[idx].count=1;
        }
      },
      cartDelMut(state,idx){ //
        state.dataCart.splice(idx,1);
      },

      buyAddMut(state,obj){  //
          let cstate = state.buyData;
          cstate.length=0;
          cstate.push(obj);
      },
      addressmut(state,data){  //
        state.xuseraddress.push(data)
      },
      addresschangemut(state,data){ //
        state.xuseraddress.splice(data.n,1,data.obj)
      },
      addressdelmut(state,n){ //
        state.xuseraddress.splice(n,1)
      },
      orderInfoMut(state,obj){ //
       
        let date = new Date();   //  
        let nowTime = date.getTime();  //
        Vue.set(obj,'orderTime',nowTime); //
        Vue.set(obj,'orderNo','1191975979853807617'); //
        Vue.set(obj,'payState',false);//false
        state.orderList.push(obj);
        console.log(obj);
      },
      updatePayState(state){ //
         let  olist=state.orderList;
         olist[olist.length-1].payState=true;
         console.log(olist);
      }


  }
  
})

