<template>
  <div class="shopping-pay">
    <div class="cart-progress">
      <div class="cart-progress-step1"></div>
      <div class="cart-progress-step2 cart-progress-step2-active"></div>
      <div class="cart-progress-step3 cart-progress-step3-active"></div>
      <div class="cart-progress-line">
        <div class="cart-progress-line-step3"></div>
      </div>
    </div>
    <div class="pay w">
      <div class="pay-headline"></div>
      <div class="pay-info">
        <div class="detail">
          
          <span>{{handlePay}}</span>&nbsp;
          <span>2353</span>
        </div>
        <div class="type">
          <ul>
            <li class="taetea-pay active">
              <div class="taetea-pay-info">
                <p class="taetea-pay-info-title">-</p>
                <div class="taetea-pay-info-balance">
                  
                  <span>0.00</span>
                  <a href="/shopping-related/wallet" class>
                    <button class="taetea-pay-recharge"></button>
                  </a>
                </div>
              </div>
            </li>
            <li class="wechat">
              <div class="wechat-title"></div>
            </li>
            <li class="alipay">
              <div class="alipay-title"></div>
            </li>
            <li class="card">
              <div class="card-title"></div>
            </li>
          </ul>
        </div>
        <div class="confirm">
          <button class="button-default pay-confirm " @click="confirmPay">&nbsp;&nbsp;&nbsp;&nbsp;</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    handlePay(){
        let orderData = this.$store.state.orderList;
        return orderData[orderData.length-1].finalPrice;
    }
  },
  methods: {
    confirmPay(){
       this.$store.commit('updatePayState');
    }
  },
};
</script>

